<template>
  <div class="comptit">
    <span class="tit-span">{{tit}}</span>
    <span class="piece-span"></span>
  </div>
</template>
<script>
export default {
  props: {
    tit: {
      type: String,
      default: ''
    }
  }
}
</script>
<style lang="less">
.comptit {
  width: 100%;
  position: relative;
  min-height: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  .tit-span {
    font-size: 16px;
    color: #333333;
    font-weight: bold;
    letter-spacing: 1px;
    position: absolute;
    z-index: 1;
  }
}
.piece-span {
  position: absolute;
  bottom: 3px;
  left: 0;
  width: 24px;
  height: 4px;
  border-radius: 2px;
  content: "";
  background-image: linear-gradient(267deg, #fe6c1a 0%, #ffd83d 100%),
    linear-gradient(#f5f7f9, #f5f7f9);
  transform: rotate(-5deg);
}
</style>



