<template>
  <!-- 搜索流程组件 -->
  <div class="sfp">
    <div class="flow-path">
      <div class="step-box">
        <div class="item" v-for="(item, key) in arr" :key="key">
          <!-- :class="item.class" -->
          <div class="step" :class="{'step-change': progress >= key}"></div>
          <div :class="{'line-orange': !(key === arr.length-1)}" v-show="progress >= key"></div>
          <div :class="{'line-gray': !(key === arr.length-1)}" v-show="(progress <= key)"></div>
          <!-- <div class="text change">{{item.text}}</div> -->
        </div>
      </div>
      <div class="step-name-box">
        <div class="item" v-for="(item, key) in arr" :key="key+item">
          <div class="text" :class="{'change': progress >= key}">{{item.text}}</div>
        </div>
      </div>
    </div>
    <div class="search" v-if="showSearch">
      <input class="search-input" placeholder="输入姓名或参赛编号搜索" />
      <div class="search-right">
        <img src="../../assets/img/search.png" class="img" />
        <div class="text">搜索</div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      progress: 1
    }
  },
  props: {
    showSearch: {
      type: Boolean,
      default: true
    },
    arr: {
      type: Array,
      default () {
        return []
      }
    }
  }
}
</script>
<style lang="less">
.sfp {
  width: 100%;
  box-sizing: border-box;
  // padding: 0 6px;
  .flow-path {
    width: 100%;
    display: flex;
    flex-direction: column;
    // align-items: center;
    // justify-content: space-between;
    margin-bottom: 12px;
    .steps-box {
      width: 100%;
    }
    .step-name-box {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .item {
        flex: 1;
        text-align: center;
        .text {
          margin-top: 10px;
          color: #999999;
          font-size: 11px;
        }
        .change {
          color: #000000;
        }
      }
    }
    .step-box {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        flex: 1;
        position: relative;
        .line-orange {
          position: absolute;
          width: 100%;
          height: 2px;
          background: #fe741d;
          top: 50%;
          left: 50%;
        }
        .line-gray {
          position: absolute;
          width: 100%;
          height: 2px;
          background: #999999;
          top: 50%;
          left: 50%;
        }
        .step {
          position: relative;
          width: 15px;
          height: 15px;
          border-radius: 50%;
          background: #999999;
          z-index: 1;
        }
        .step-change {
          background: #fe741d;
        }
      }
    }
  }
  .search {
    width: 100%;
    background: #f8f8f8;
    // background: saddlebrown;
    border-radius: 17px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    padding: 5px;
    .search-input {
      border: none;
      outline: none;
      padding: 5px 0 5px 5px;
      font-size: 14px;
      color: #333333;
      // width: 80%;
      flex: 1;
      background: #f8f8f8;
      margin-left: 20px;
    }
    .search-right {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 20px;
      .img {
        width: 12px;
        height: 12px;
        margin-right: 10px;
      }
      .text {
        font-size: 14px;
        color: #fe741d;
        font-weight: bold;
      }
    }
  }
}
</style>



