<template>
  <!-- 大赛规则和背景和info -->
  <div class="gameInfo">
    <div class="avatar-box-info" v-if="showAvatarInfo">
      <div class="avatar-box">
        <el-image :src="juryInfo.avatar" class="avatar" fit="cover"></el-image>
      </div>
      <div class="baseInfo">
        <div class="name">评委姓名：{{juryInfo.name}}</div>
        <div class="phone">联系电话：{{resetPhones(juryInfo.mobile)}}</div>
      </div>
    </div>
    <div class="content-info">
      <div class="title">
        <title-comp :tit='contentInfo.title'></title-comp>
      </div>
      <div class="info">{{juryInfo.desc}}</div>
    </div>
  </div>
</template>
<script>
import { resetPhone } from '../../tools/commons'
import titleComp from '../titleComp/titleComp'
export default {
  components: {
    titleComp
  },
  methods: {
    resetPhones (num) {
      return resetPhone(num)
    }
  },
  computed: {
    // ...mapState({
    //   juryInfo: state => state.admin.juryDescribe.juryInfo
    // })
  },
  mounted () {
    // console.log(resetPhone, 'resetPhone')
  },
  props: {
    juryInfo: {
      type: Object,
      default () {
        return {}
      }
    },
    showAvatarInfo: {
      type: Boolean,
      default: true
    },
    headerInfo: {
      type: Object,
      default () {
        return {}
      }
    },
    contentInfo: {
      type: Object,
      default () {
        return {}
      }
    }
  }
}
</script>
<style lang="less">
.gameInfo {
  width: 100%;
  .avatar-box-info {
    background: #ffffff;
    border-radius: 4px;
    padding: 10px;
    box-sizing: border-box;
    width: 100%;
    box-shadow: 0px -1px 5px 0px rgba(196, 196, 196, 0.2),
      0px 2px 8px 0px rgba(226, 226, 226, 0.5);
    border: solid 1px #f1f1f1;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    .avatar-box {
      margin-right: 17px;
      .avatar {
        width: 60px;
        height: 60px;
        border-radius: 50%;
      }
    }
    .baseInfo {
      .name,
      .phone {
        font-size: 14px;
        color: #333333;
        letter-spacing: 1px;
      }
      .name {
        margin-bottom: 8px;
      }
    }
  }
  .content-info {
    padding-top: 18px;
    .title {
      font-size: 16px;
      color: #333333;
      font-weight: bold;
      letter-spacing: 1px;
      padding-bottom: 18px;
    }
    .info {
      font-size: 14px;
      color: #333333;
      letter-spacing: 1px;
      text-align: justify;
      line-height: 1.5;
    }
  }
}
</style>



