<template>
  <div class="title">
    <!-- <img class="pic" src="@/assets/img/title.png"/> -->
    {{text}}
  </div>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      default: ''
    }
  }
}
</script>

<style lang="less" scoped>
.title {
  width: 100vw;
  text-align: center;
  padding: 0 5vw;
  font-size: 16px;
  color: #ff5700;
  font-family: PingFang-SC-Heavy;
  font-weight: bold;
  line-height: 14*1.5px;
  text-align: center;
  .pic {
    width: 90vw;
  }
}
</style>



