<template>
  <!-- 外层布局 -->
  <div class="layout">
    <!-- style="{ height: topHeight + 'rpx' }" -->
    <div class="top">
      <slot name="top"></slot>
    </div>
    <div class="content">
      <slot name="content"></slot>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    topHeight: {
      type: [String, Number],
      default: 350
    }
  }
}
</script>
<style lang="less">
.layout {
  width: 100%;
  .top {
    width: 100%;
  }
  .content {
    width: 100%;
  }
}
</style>



